import React, {FC, memo, useEffect, useState} from "react";
import {MessagesList} from "./MessagesList/MessagesList";
import {Editor} from "./Editor/Editor";
import {MessageType} from "../../../redux/messages/MessagesReducer";

export const Messages: FC<MessagesType> = memo((
   {
      userIdFromURL,
      messages,
      sendMessageCallback,
   }) => {
   const [state, setState] = useState({
      submitting: false,
      value: '',
   })

   const handleSubmit = async () => {
      if (!state.value) {
         return;
      }

      setState({
         ...state,
         submitting: true,
      });
      await sendMessageCallback(value)
         .then(() => {
            setState({
               submitting: false,
               value: '',
            });
         })
         .catch(() => {
            console.log("error")
         })
         .finally(() => {
            setState({
               ...state,
               submitting: false,
            });
         })

   }

   const handleChange = (e: any) => {
      setState({
         ...state,
         value: e.target.value,
      });
   };

   const {submitting, value} = state;
   return (
      <>
         {messages.length > 0 && <MessagesList messages={messages}/>}
         <Editor
            onChange={handleChange}
            onSubmit={handleSubmit}
            submitting={submitting}
            value={value}
         />
      </>
   );
})

export type MessagesType = {
   userIdFromURL: string
   messages: MessageType[]
   sendMessageCallback(message: string): Promise<any>
}


